'use client'

import { useState, useEffect } from 'react'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Badge } from '@/components/ui/badge'
import { Progress } from '@/components/ui/progress'
import { apiHelpers } from '@/lib/apiClient'

interface CareerRecommendation {
  id: string
  title: string
  description: string
  matchPercentage: number
  requiredSkills: string[]
  averageSalary: string
  growthOutlook: string
  reasons: string[]
}

interface UserProfile {
  name: string
  currentLevel: number
  xpPoints: number
  completedAssessments: string[]
}

export default function CareerPage() {
  const [recommendations, setRecommendations] = useState<CareerRecommendation[]>([])
  const [userProfile, setUserProfile] = useState<UserProfile | null>(null)
  const [loading, setLoading] = useState(true)
  const [refreshing, setRefreshing] = useState(false)
  const [error, setError] = useState<string | null>(null)

  // Mock data for demonstration
  const mockRecommendations: CareerRecommendation[] = [
    {
      id: '1',
      title: 'Data Scientist',
      description: 'Analyze complex data to help organizations make informed decisions using statistical methods and machine learning.',
      matchPercentage: 92,
      requiredSkills: ['Python', 'Machine Learning', 'Statistics', 'SQL', 'Data Visualization'],
      averageSalary: '$95,000 - $140,000',
      growthOutlook: 'Much faster than average (22%)',
      reasons: [
        'Strong analytical thinking from your assessments',
        'Interest in problem-solving and patterns',
        'Mathematical background aligns well'
      ]
    },
    {
      id: '2',
      title: 'UX/UI Designer',
      description: 'Create intuitive and engaging user experiences for digital products and applications.',
      matchPercentage: 87,
      requiredSkills: ['Design Thinking', 'Figma', 'User Research', 'Prototyping', 'HTML/CSS'],
      averageSalary: '$70,000 - $110,000',
      growthOutlook: 'Faster than average (13%)',
      reasons: [
        'Creative problem-solving abilities',
        'Strong communication skills',
        'Interest in human behavior and psychology'
      ]
    },
    {
      id: '3',
      title: 'Product Manager',
      description: 'Guide product development from conception to launch, working with cross-functional teams.',
      matchPercentage: 84,
      requiredSkills: ['Strategic Thinking', 'Project Management', 'Market Research', 'Communication', 'Analytics'],
      averageSalary: '$85,000 - $130,000',
      growthOutlook: 'Faster than average (19%)',
      reasons: [
        'Leadership potential identified',
        'Strong organizational skills',
        'Ability to see big picture while managing details'
      ]
    }
  ]

  const mockUserProfile: UserProfile = {
    name: 'Alex Thompson',
    currentLevel: 7,
    xpPoints: 2450,
    completedAssessments: ['RIASEC', 'MBTI', 'Skills Assessment']
  }

  useEffect(() => {
    const fetchCareerData = async () => {
      try {
        setLoading(true)
        setError(null)
        
        // Try to fetch from API, fallback to mock data
        try {
          const [careerData, profileData] = await Promise.all([
            apiHelpers.get<CareerRecommendation[]>('/career/recommendations'),
            apiHelpers.get<UserProfile>('/profile')
          ])
          setRecommendations(careerData)
          setUserProfile(profileData)
        } catch (apiError) {
          // Use mock data if API is not available
          console.log('API not available, using mock data')
          setRecommendations(mockRecommendations)
          setUserProfile(mockUserProfile)
        }
      } catch (err) {
        setError('Failed to load career recommendations')
        setRecommendations(mockRecommendations)
        setUserProfile(mockUserProfile)
      } finally {
        setLoading(false)
      }
    }

    fetchCareerData()
  }, [])

  const handleRefreshRecommendations = async () => {
    try {
      setRefreshing(true)
      setError(null)
      
      // Simulate API call delay
      await new Promise(resolve => setTimeout(resolve, 1000))
      
      // Use mock data for refresh
      setRecommendations(mockRecommendations)
    } catch (err) {
      setError('Failed to refresh recommendations')
    } finally {
      setRefreshing(false)
    }
  }

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-96">
        <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-primary"></div>
      </div>
    )
  }

  return (
    <div className="max-w-6xl mx-auto">
      <div className="mb-8">
        <h1 className="text-3xl font-bold text-foreground mb-2">AI Career Guidance</h1>
        <p className="text-muted-foreground">
          Personalized career recommendations powered by AI analysis of your skills, interests, and assessments
        </p>
      </div>

      {/* User Profile Summary */}
      {userProfile && (
        <Card className="mb-8">
          <CardHeader>
            <CardTitle>Your Progress</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
              <div>
                <p className="text-sm text-muted-foreground mb-1">Current Level</p>
                <p className="text-2xl font-bold text-foreground">{userProfile.currentLevel}</p>
              </div>
              <div>
                <p className="text-sm text-muted-foreground mb-1">XP Points</p>
                <p className="text-2xl font-bold text-foreground">{userProfile.xpPoints.toLocaleString()}</p>
              </div>
              <div>
                <p className="text-sm text-muted-foreground mb-1">Completed Assessments</p>
                <div className="flex flex-wrap gap-1 mt-2">
                  {userProfile.completedAssessments.map((assessment) => (
                    <Badge key={assessment} variant="secondary">
                      {assessment}
                    </Badge>
                  ))}
                </div>
              </div>
            </div>
          </CardContent>
        </Card>
      )}

      {/* Refresh Button */}
      <div className="mb-6 flex justify-between items-center">
        <h2 className="text-xl font-semibold text-foreground">Your Career Recommendations</h2>
        <Button 
          onClick={handleRefreshRecommendations} 
          disabled={refreshing}
          variant="outline"
        >
          {refreshing ? 'Refreshing...' : 'Refresh Recommendations'}
        </Button>
      </div>

      {error && (
        <Card className="mb-6 border-destructive">
          <CardContent className="pt-6">
            <p className="text-destructive">{error}</p>
          </CardContent>
        </Card>
      )}

      {/* Career Recommendations */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {recommendations.map((recommendation) => (
          <Card key={recommendation.id} className="hover:shadow-md transition-shadow">
            <CardHeader>
              <div className="flex items-center justify-between">
                <CardTitle className="text-lg">{recommendation.title}</CardTitle>
                <div className="text-right">
                  <div className="text-2xl font-bold text-primary">
                    {recommendation.matchPercentage}%
                  </div>
                  <div className="text-xs text-muted-foreground">match</div>
                </div>
              </div>
              <Progress value={recommendation.matchPercentage} className="mt-2" />
            </CardHeader>
            <CardContent className="space-y-4">
              <p className="text-muted-foreground">{recommendation.description}</p>
              
              <div>
                <h4 className="font-medium text-foreground mb-2">Required Skills</h4>
                <div className="flex flex-wrap gap-1">
                  {recommendation.requiredSkills.map((skill) => (
                    <Badge key={skill} variant="outline">
                      {skill}
                    </Badge>
                  ))}
                </div>
              </div>

              <div className="grid grid-cols-1 sm:grid-cols-2 gap-4 text-sm">
                <div>
                  <p className="font-medium text-foreground">Average Salary</p>
                  <p className="text-muted-foreground">{recommendation.averageSalary}</p>
                </div>
                <div>
                  <p className="font-medium text-foreground">Growth Outlook</p>
                  <p className="text-muted-foreground">{recommendation.growthOutlook}</p>
                </div>
              </div>

              <div>
                <h4 className="font-medium text-foreground mb-2">Why this matches you</h4>
                <ul className="text-sm text-muted-foreground space-y-1">
                  {recommendation.reasons.map((reason, index) => (
                    <li key={index} className="flex items-start">
                      <span className="mr-2">•</span>
                      <span>{reason}</span>
                    </li>
                  ))}
                </ul>
              </div>

              <Button className="w-full mt-4">
                Learn More About This Career
              </Button>
            </CardContent>
          </Card>
        ))}
      </div>

      <div className="mt-8 text-center">
        <Card className="bg-muted">
          <CardContent className="pt-6">
            <h3 className="font-semibold text-foreground mb-2">Want more personalized recommendations?</h3>
            <p className="text-muted-foreground mb-4">
              Complete additional assessments to get even better career matches
            </p>
            <Button variant="outline">
              Take More Assessments
            </Button>
          </CardContent>
        </Card>
      </div>
    </div>
  )
}
